.searchForm{
    @include flex(column, center, center, center);
    background-image:linear-gradient( to bottom, #fff0 80%, $white), url("../assets/searchBackground.jpg");
    padding-top: 2rem;
    min-height: 100vh;
    background-position: center center;
    background-size: cover;
}
.smallWrapper {
    padding:120px 100px;
    border-radius: 10px;

    background-color:rgba(0, 0, 0, 0.6);
}
.radioButtons {
    @include flex(row, space-between, center, center);
    width:100%;
    margin-bottom: 10px;
    
}

.radioButtonLabel {
    @include color($secondaryColor, $white);
    width:calc(100% / 3);
    padding:8px 10px;
    text-align: center;
    border:1px solid $white;
    border-radius: 2px;
    transition: 0.2s;
    box-shadow: 0 5px #999;
    display: block;
    font-size: 20px;
    font-weight: 700;
}

.radioButtonDot:checked + label {
    transform: translateY(3px);
    box-shadow: 0 2px #666;
    @include color($activeColor, $black);
}

.radioButtonDot {
    display: none;
}

.userInput, 
.userInputLabel {
    margin-top: 20px;
    font-size: 25px;
}
.userInput,
.userInput::placeholder {
    font-family: $bodyFont;
}
.userInput {
    @include color($white, $black);
    margin-left: 20px;
}

.schoolsListSearchInput {
    min-height: auto;
    width: 50%;
    margin: 0 auto;
    @include flex(column, center, center, center)
}

.schoolsListSearch {
    margin: 15px auto;
    text-align: center;
    width: 80%;
}
